body{color:#9da4b4;background-color:#f3f3f3;font-family:'Baloo Paaji 2', cursive;font-family:'Roboto Mono', monospace}.search-input{max-width:500px;width:100%;margin:0 auto;display:flex;flex-direction:row;justify-content:start}.search-input input{outline:none;font-size:16px;padding:3px;width:100%}.search-input .clear-button{background-color:silver;color:white;cursor:pointer}.cards{margin:0 auto;width:100%;max-width:500px;display:flex;flex-direction:column;justify-content:start;font-size:16px}.cards .card{background-color:white;border-radius:4px;margin:2px 0;color:#9da4b4}.cards .card .card-title{padding:5px;font-weight:bold;font-size:1.0em}.cards .card .card-data{padding:5px;color:#bcc3c9}.cards .card .card-tags{padding:5px}
